import { Component, Input } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { SectionType } from '@core/enums';
import { Ad, Tenant } from '@core/models';
import { environment } from '@environments/environment';
import { MatchingTenantsFacade } from '@shared/facades/matching-tenants-facade';
import { Observable } from 'rxjs';

@Component({
    selector: 'findqo-matching-tenants',
    templateUrl: './matching-tenants.component.html',
    styleUrl: './matching-tenants.component.scss',
    standalone: false
})
export class MatchingTenantsComponent {
	constructor(private readonly matchingTenantsFacade: MatchingTenantsFacade) {
		this.matchingtenants$ = this.matchingTenantsFacade.matchingTenantsList$;
	}

	@Input() ad: Ad;

	readonly sectionType = SectionType;
	appRoute = APP_ROUTE;
	matchingtenants$: Observable<Tenant[]>;

	readonly defaultAvatar = environment.defaultAvatarImg;
}
