import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, PLATFORM_ID } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { APP_ROUTE, BER_EXEMPT, MY_ADS_HEADER_STATUS_MAPPING } from '@core/constants';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad, AgentHubAction, Brochure, Translations } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { Observable } from 'rxjs';
import { ActionsComponent } from '../../components/actions/actions.component';
import { AD_DETAIL_ACTIONS } from '@core/constants/agent-hub-actions.constant';
import { AdShareDialogComponent } from '@shared/components/ads/ad-share-dialog/ad-share-dialog.component';
import { WindowService } from '@core/services/window.service';
import { MatDialog } from '@angular/material/dialog';
import { BrowserUtility } from '@core/services/browser-utility.service';
import { MyAdDeleteDialogComponent } from '@feature/my-ads/components/my-ad-delete-dialog/my-ad-delete-dialog.component';
import { ActivatedRoute, Router } from '@angular/router';
import { AgentHubActions } from '@core/enums/agent.enum';
import { MatchingTenantsFacade } from '@shared/facades/matching-tenants-facade';
import { SnackService } from '@core/services/snack.service';
import { MyAdUnPublishDialogComponent } from '@feature/my-ads/components/my-ad-unpublish-dialog/my-ad-unpublish-dialog.component';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { GoogleFeedbackDialogComponent } from '@feature/my-ads/components/google-feedback-dialog/google-feedback-dialog.component';
import { StorageService } from '@core/services/storage.service';
import { UpdateStatusDialogComponent } from '@feature/my-ads/components/updateStatusDialog/update-status-dialog.component';

@Component({
    selector: 'findqo-ad-detail',
    templateUrl: './ad-detail.page.html',
    styleUrl: './ad-detail.page.scss',
    standalone: false
})
export class AdDetailPage {
	constructor(
		private readonly commonFacade: CommonFacade,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly bottomSheet: MatBottomSheet,
		private readonly window: WindowService,
		private readonly matDialog: MatDialog,
		private readonly utility: BrowserUtility,
		private readonly router: Router,
		private readonly tenantFacade: MatchingTenantsFacade,
		@Inject(PLATFORM_ID) public platformId,
		private readonly route: ActivatedRoute,
		private readonly snack: SnackService,
		private myAdsFacade: MyAdsFacade,
		private storage: StorageService
	) {
		this.translations$ = this.commonFacade.getTranslations$();
		this.ad$ = this.sharedAdsFacade.getAd$();
		this.isLoading$ = this.sharedAdsFacade.isLoading$();
	}

	ad$: Observable<Ad>;
	isLoading$: Observable<boolean>;
	readonly berExempt = BER_EXEMPT;
	readonly actionOption: (ad: Ad) => AgentHubAction[] = AD_DETAIL_ACTIONS;
	readonly appRoute = APP_ROUTE;

	currentSection = 'properties-for-rent';
	translations$: Observable<Translations>;
	buttonType = ButtonType;
	isLoading = false;
	loaderRef = null;

	onDownloadBrochure(brochure: Brochure): void {
		window.open(brochure?.name, '_blank');
	}

	openActionDialog(ad: Ad): void {
		const ref = this.bottomSheet.open(ActionsComponent, {
			panelClass: ['agent-ad-detail'],
		});

		ref.instance.ad = ad;
		ref.instance.isBottomActionbar = true;
		ref.instance.dialogRef = ref;
		ref.instance.options = this.actionOption(ad);

		ref.afterDismissed().subscribe((res) => {
			if (res?.reference) {
				this.handleActionButton(res, ad);
			}
		});
	}

	handleActionButton(actionItem: AgentHubAction, ad: Ad): void {
		if (actionItem.reference && actionItem.reference !== AgentHubActions.DELETE_AD) {
			this.isLoading = true;
			this.loaderRef = actionItem.reference;
		}

		switch (actionItem.reference) {
			case AgentHubActions.RENEW_AD:
				this.router.navigate([actionItem.routerLink]).finally(() => (this.isLoading = false));

				break;
			case AgentHubActions.EDIT:
				this.router.navigate([actionItem.routerLink]).finally(() => (this.isLoading = false));

				break;

			case AgentHubActions.VIEW_AD_ON_WEB:
				this.openAd(actionItem.link);
				this.isLoading = false;
				break;

			case AgentHubActions.SHARE_AD:
				this.shareAd(ad);
				this.isLoading = false;
				break;
			case AgentHubActions.DELETE_AD:
				this.deleteAd(ad);
				break;
			case AgentHubActions.UNPUBLISH_AD:
				this.UnPublishAd(ad);
				break;
			case AgentHubActions.PUBLISH_AD:
				this.PublishAd(ad);
				break;

			/**
			 * Todo: Need to make all below functions as common.
			 */

			case AgentHubActions.MARK_AS_OCCUPIED:
				this.markAsOccupied(ad);
				break;
			case AgentHubActions.MARK_AS_AVAILABLE:
				this.markAsAvailable(ad);
				break;

			case AgentHubActions.MARK_AS_SALE_AGREED:
				this.markAsSaleAgreed(ad);
				break;

			case AgentHubActions.MARK_AS_SOLD:
				this.markAsSold(ad);
				break;

			default:
		}
	}

	markAsOccupied(ad) {
		this.matDialog
			.open(UpdateStatusDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				data: { adId: ad.id, myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status), status: 'property-occupied' },
			})
			.afterClosed()
			.subscribe((res) => {
				this.isLoading = false;

				if (!res) {
					return;
				}

				this.sharedAdsFacade.updateAd({ adStatus: { status: 'property-occupied' } });
				this.snack.open({ data: { message: 'This property is now marked as Occupied.', type: 'success' }, panelClass: ['snackbar-success'] });

				if (this.storage.getItem('isAdFeedbackSet') !== 'true') {
					this.matDialog
						.open(GoogleFeedbackDialogComponent, {
							disableClose: true,
							maxWidth: '350px',
						})
						.afterClosed()
						.subscribe((res) => {
							if (res) {
								this.storage.setItem('isAdFeedbackSet', 'true');
							}
						});
				}
			});
	}

	markAsAvailable(ad) {
		this.matDialog
			.open(UpdateStatusDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				data: { adId: ad.id, myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status), status: 'live' },
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					this.sharedAdsFacade.updateAd({ adStatus: { status: 'live' } });
					this.snack.open({ data: { message: 'This property is now marked as Occupied.', type: 'success' }, panelClass: ['snackbar-success'] });
				}
				this.isLoading = false;
			});
	}

	markAsSaleAgreed(ad) {
		this.matDialog
			.open(UpdateStatusDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				data: { adId: ad.id, myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status), status: 'sale-agreed' },
			})
			.afterClosed()
			.subscribe((res) => {
				this.isLoading = false;

				if (!res) {
					return;
				}

				this.sharedAdsFacade.updateAd({ adStatus: { status: 'sale-agreed' } });
				this.snack.open({ data: { message: 'This property is now marked as Sale Agreed.', type: 'success' }, panelClass: ['snackbar-success'] });

				if (this.storage.getItem('isAdFeedbackSet') !== 'true') {
					this.matDialog
						.open(GoogleFeedbackDialogComponent, {
							disableClose: true,
							maxWidth: '350px',
						})
						.afterClosed()
						.subscribe((res) => {
							if (res) {
								this.storage.setItem('isAdFeedbackSet', 'true');
							}
						});
				}
			});
	}

	markAsSold(ad) {
		this.matDialog
			.open(UpdateStatusDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				data: { adId: ad.id, myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status), status: 'sold' },
			})
			.afterClosed()
			.subscribe((res) => {
				this.isLoading = false;

				if (!res) {
					return;
				}

				this.sharedAdsFacade.updateAd({ adStatus: { status: 'sold' } });
				this.snack.open({ data: { message: 'This property is now marked as Sold.', type: 'success' }, panelClass: ['snackbar-success'] });

				if (this.storage.getItem('isAdFeedbackSet') !== 'true') {
					this.matDialog
						.open(GoogleFeedbackDialogComponent, {
							disableClose: true,
							maxWidth: '350px',
						})
						.afterClosed()
						.subscribe((res) => {
							if (res) {
								this.storage.setItem('isAdFeedbackSet', 'true');
							}
						});
				}
			});
	}

	openAd(adLink: string): void {
		const OPENED_TAB = this.window.reference.open(adLink, '_blank');

		// If popup window is disabled from the settings
		if (!OPENED_TAB || OPENED_TAB.closed || typeof OPENED_TAB.closed === 'undefined') {
			this.snack.open({
				data: { translationKey: 'n3965', type: 'error' },
				panelClass: ['snackbar-error'],
			});
		}
	}

	shareAd(ad): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		const AVALABLE_FROM = ad.availableFrom ? TRANSLATIONS?.['n3111'] + ':' + this.commonFacade.getFormattedDate(ad.availableFrom) : '';

		// Sharing ad from Ad detail page
		if (this.utility.isWebShareSupported()) {
			navigator
				.share({
					title: ad?.title,
					text: `${TRANSLATIONS?.['n152']}:  ${ad.price.isPriceOnApplication ? TRANSLATIONS?.['n3311'] : ad.price.symbol + ' ' + ad.price.minValue + ' ' + (ad.price.maxValue ? ' - ' + ad.price.maxValue : '')}\n${AVALABLE_FROM}`,
					url: ad?.adlink ?? this.window.reference.location.href,
				})
				.then(() => {
					// TODO: Do we need snackbar here?
					console.log('Content shared successfully!');
				})
				.catch((error) => {
					console.error('Error sharing content:', error);
				});
		} else {
			this.matDialog
				.open(AdShareDialogComponent, {
					disableClose: true,
					maxWidth: '407px',
					data: { adLink: ad.adlink },
				})
				.afterClosed();
		}
	}

	deleteAd(ad): void {
		this.matDialog
			.open(MyAdDeleteDialogComponent, {
				disableClose: true,
				maxWidth: '407px',
				data: { adId: ad.id, myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status) },
			})
			.afterClosed()
			.subscribe((res) => {
				this.isLoading = false;

				if (res) {
					this.router.navigate([APP_ROUTE.agentHub.dashboard]);
				}
			});
	}

	UnPublishAd(ad): void {
		this.matDialog
			.open(MyAdUnPublishDialogComponent, {
				disableClose: true,
				maxWidth: '407px',
				data: { adId: ad.id, updatestatus: 'unpublished', myAdHeaderStatusId: this.getStatusIndex(ad.adStatus.status) },
			})
			.afterClosed()
			.subscribe((res) => {
				if (res === 'UPDATE_STATUS') {
					this.sharedAdsFacade.updateAd({ adStatus: { status: 'unpublished' } });
				}
				this.isLoading = false;
			});
	}
	PublishAd(ad): void {
		this.myAdsFacade.publishAd(ad.id, 'live', this.getStatusIndex(ad.adStatus.status));
		this.sharedAdsFacade.updateAd({ adStatus: { status: 'live' } });
	}
	private getStatusIndex(status) {
		const index = Object.keys(MY_ADS_HEADER_STATUS_MAPPING).find((key) => MY_ADS_HEADER_STATUS_MAPPING[key] === status);
		return index ? Number.parseInt(index) : null;
	}

	ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.tenantFacade.loadTenantList(this.route.snapshot.params['adId']).subscribe();
		}
	}
}
