import { isPlatformBrowser } from '@angular/common';
import { Component, ElementRef, Inject, Input, PLATFORM_ID, ViewChild } from '@angular/core';
import { Images } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import Swiper, { Keyboard, Navigation, Zoom } from 'swiper';
import { register } from 'swiper/element';

@Component({
    selector: 'findqo-ad-photos',
    templateUrl: './ad-photos.component.html',
    styleUrl: './ad-photos.component.scss',
    standalone: false
})
export class AdPhotosComponent {
	constructor(
		@Inject(PLATFORM_ID) public plateformId,
		protected viewport: ViewportService
	) {}
	@ViewChild('swiperContainer') swiperSlideShow!: ElementRef;

	@Input() adImages: Images[];

	slidesPerView = 3;
	mySwiper: Swiper;
	timer = null;

	ngAfterViewInit(): void {
		// Some time swiperSlideShow reference is being null and autoplay is not getting triggered
		if (this.swiperSlideShow && isPlatformBrowser(this.plateformId)) {
			this.mySwiper = new Swiper(this.swiperSlideShow.nativeElement, {
				modules: [Keyboard, Navigation, Zoom],
				keyboard: {
					enabled: true,
				},
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
				},
				spaceBetween: 16,
				slidesPerView: 'auto',
				centeredSlides: false,
				zoom: {
					toggle: false,
				},
				loop: false,
			});
		}
	}

	ngOnInit(): void {
		register();
		this.slidesPerView = this.viewport.mobile ? 1 : 3;
	}
}
