import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdDetailPage } from './ad-detail.page';
import { PrivateAdDetailResolver } from '@feature/agent-hub/resolvers/ad-detail.resolver';

const routes: Routes = [
	{
		path: ':adId',
		component: AdDetailPage,
		resolve: { adResolver: PrivateAdDetailResolver },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class AdDetailPageRoutingModule {}
