import { isPlatformBrowser } from '@angular/common';
import { inject, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, ResolveFn, Router, RouterStateSnapshot } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { Ad } from '@core/models';
import { SnackService } from '@core/services/snack.service';
import { AdsState } from '@shared/states/ads.state';
import { Observable, catchError, finalize, of, switchMap, tap } from 'rxjs';

// This is to use on Private routes only
export const PrivateAdDetailResolver: ResolveFn<any> = (
	route: ActivatedRouteSnapshot,
	_state: RouterStateSnapshot,
	adsApi: AdsApi = inject(AdsApi),
	adsState = inject(AdsState),
	router = inject(Router),
	platformId = inject(PLATFORM_ID),
	snack = inject(SnackService)
): Observable<any> => {
	adsState.setIsLoading(true);

	if (isPlatformBrowser(platformId)) {
		return adsApi.getAdDetail(route.params['adId']).pipe(
			catchError((error) => {
				snack.open({
					data: { message: error?.error?.error?.id },
					panelClass: 'snackbar-error',
				});

				adsState.setAd(undefined);
				router.navigate([APP_ROUTE.agentHub.dashboard, APP_ROUTE.agentHub.listings]);
				return of(null);
			}),
			tap((ad: Ad) => adsState.setAd(ad)),
			finalize(() => adsState.setIsLoading(false))
		);
	}

	return of(null);
};
